import { Injectable } from  '@angular/core';
import { Http} from  '@angular/http';
import { Coupon} from '../common/coupon';
@Injectable({
  providedIn: 'root'
})
export class CustomerServiceService {
  coupons : Coupon[] ;
  constructor(private _http:Http) { }

  buyCoupon(id : number){
  	this._http.get("http://localhost:8082/customer/purchaseCoupon/" +  id).subscribe(
  		resp=>{
  			console.log(resp.status);
  		})
  	
  }

  getAllCoupon(){
    this._http.get("http://localhost:8082//customer/getAllCoupons/").subscribe(
      resp=>{
        this.coupons = resp.json();
        console.log(resp);
      })
  }

}
